::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 10px rgb(233, 9, 83);
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background: rgb(245, 15, 103);
  box-shadow: inset 0 0 6px rgba(19, 19, 19, 0.5);
}

::-webkit-scrollbar-thumb:hover {
  background: rgb(196, 2, 60);
}

.dropdown:hover .dropdown-menu {
  display: block;
}
.emoji-404 {
  position: relative;
  animation: mymove 2.5s infinite;
}

@keyframes mymove {
  33% {
    top: 0px;
  }
  66% {
    top: 20px;
  }
  100% {
    top: 0px;
  }
}
